@media screen {
  .landing {
    background-color: #36454f;
    min-height: 100vh;
    padding-top: 10px;

    &--loading {
      filter: brightness(50%);
    }
  }

  .contractor {
    background-color: beige;
    margin-bottom: 10px;
  }

  .contractor__container {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .contractor__form {
    padding-bottom: 5px;
  }

  .contractor__form__input {
    padding: 5px 10px;
  }

  .order__controls {
    height: 100%;
  }

  .order__controls__button {
    width: 100%;
  }

  .order__controls__button:last-child {
    width: 100%;
    flex-grow: 1;
  }

  .order__form {
    background-color: beige;
  }

  .order__form__table__product__select {
    padding: 5px 10px;
    text-overflow: ellipsis;
    max-width: 10ch;
  }

  .order__form__table__product__input {
    padding: 5px 10px;
    min-width: 10ch;
  }

  .progress__spinner {
    position: absolute;
    top: calc(50% - 20px);
    left: calc(50% - 20px);
  }
}
